
:root {
  --color-background1: #FFF7F2; /* 안즈리화 */
  --color-background2: #FFE4E1; /* 파우더핑크 F4EBE7 크리스마스스노우베이지 */
  --color-background-menu: #F9EBDA; /* 노블레스아이보리 */
  --color-background-footer: #261A1A; /* 베인다크초코 */

  --color-button-hover: #21825B;

  --color-text: #261A1A; /* 베인다크초코 */
  --color-text-title: #FFF7F2; /* 안즈리화 */
  --color-text-title-background: #261A1A; /* 베인다크초코 */
  --color-text-hyberlink: #253A84; /* 튤립파랑 */
  --color-text-menu: #261A1A; /* 베인다크초코 */
  --color-text-footer: #FFF7F2; /* 안즈리화 */

  --font-size-lg: clamp(2rem, 4vw, 3.5rem);
  --font-size-md: clamp(1.5rem, 3vw, 2.2rem);
  --font-size-sm: clamp(1.3rem, 2vw, 1.8rem);

  --image-size-content-width: 80%;

  --icon-size: clamp(2rem, 3vw, 3rem);

  /*
  --image-size-content-width: clamp(90%, 2vw, 50%);
  font-size: var(--font-size-lg);
  */
  --font-common: 'RIDIBatang';

}


  .Common-all {
    text-align: center;
    word-break: keep-all;
    font-family: var(--font-common);
  }

  .Common-header {
    background-color: var(--color-background2);
/*    min-height: 100vh;*/
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    font-size: 10rem;
/*    color: white;*/
  }

  .Common-sub-header {
    /* position: absolute; */
    background-color: var(--color-background2);

    /*    min-height: 100vh;*/
/*    height:100px; */
    height: auto;
    /* display: flex; */
    /* flex-direction: row; */
    /* margin-left: auto;
    margin-right: auto; */
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    font-size: var(--font-size-md);
/*  
    font-size: 10rem;

color: white;*/
/*    border-bottom: white solid 1px;*/
  }

  .Common-body {

/*
    background-color: #282c34;
min-height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }

  .Common-footer {
    /* border-top: 1px solid #261A1A;
    margin-left: 10px; */
    /* background-color: white; */
    background-color: var(--color-background-footer);
    color: var(--color-text-footer);
    height: 16vmin;
/*    min-height: 100vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    /* color: black; */
    color: #261A1A;
  }

  .Common-top-title {
    font-family: 'RIDIBatang';
    font-size: var(--font-size-lg);

    display: flex;
    flex-direction: column;

    /* margin-top: 10px; 
    
        padding-top: 2vh;
    padding-bottom: 2vh;

    */
    color: var(--color-text);
    background-color: var(--color-background1);
  }

  .Common-sub-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
    /* margin-bottom: 30px; */
  }

  .Common-content-background-1 {
    background-color: var(--color-background1);
  }

  .Common-content-background-2 {
    background-color: var(--color-background2);
  }

  .Common-sub-title {
    font-family: 'RIDIBatang';
    /* font-size: calc(10px + 2vmin); font-size: 2.5rem; */
    
    font-size: var(--font-size-md);
    width: fit-content;
    text-align: center; 
    margin: 0 auto;
    color: var(--color-text-title);
    background-color: var(--color-text-title-background);
    
    /* 
    margin-bottom: 20px;
    background: linear-gradient(to top, #c7c1b9 30%, transparent 30%);
    background: linear-gradient(to top, #353841 35%, transparent 35%); */
    /* background: linear-gradient(to top, #FFE400 35%, transparent 35%); */
  }
  
  .Common-sub-content {
    font-family: var(--font-common);
    color: var(--color-text);
    font-size: var(--font-size-sm);
    white-space: pre-wrap;
    padding-left: 20px;
    padding-right: 20px;
  }

  .Common-sub-content-footer {
    font-family: var(--font-common);
    color: var(--color-text-footer);
    font-size: var(--font-size-sm);
    white-space: pre-wrap;
    padding-left: 20px;
    padding-right: 20px;
  }

  .Common-sub-description {
    font-family: var(--font-common);
    font-size: var(--font-size-sm);
    padding-left: 15px;
    padding-right: 15px;
    white-space: pre-wrap;
  }

  .Common-empty-area-50 {
    height: 50px;
  }
  .Common-empty-area-20 {
    height: 20px;
  }

  .Common-empty-area-width-20 {
    width: 20px;
  }

  .Common-empty-area-width-80pecent {
    width: 80%;
  }

  .Common-content-block {
    width: 100%;
  }
  
  .Common-content-img {
/*    width: 33%;*/
    width: var(--image-size-content-width);
    margin-bottom: 20px;
  }

  .Common-link {
    color: var(--color-text-hyberlink);
    /* # 61dafb 
    font-family: 'RIDIBatang';
    
        font-size: calc(5px + 2vmin);
*/
    font-size: var(--font-size-sm);
    font-family: var(--font-common);

  }

  .Common-title-border {
    width : 100%;
    padding-bottom : 10%;
    background-image : url('../img/title_border.png');  
    /* position: ;  */
    background-size : contain;
    background-repeat : no-repeat;
    background-position: center;
  }

  .Common-back-btn {
    position: absolute;
    height : 6vmin;
    width : 6vmin;
    top: 1vh;
    left: 1vh;
    filter: invert(100%);
    background-image : url('../img/arrow.png');  
    background-size : cover;
    color: white;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-home-btn {
    position: absolute;
    height : 5vmin;
    width : 5vmin;
    top: 3vmin;
    left: 3vmin;
    filter: invert(100%);
    background-image : url('../img/home.png');  
    background-size : cover;
    color: white;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-fixed-top-btn {
    position: fixed;
    height : 5vmin;
    width : 5vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    filter: invert(100%);
    opacity: 0.5;

    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    color: white;
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content:right;
  }

  .Common-absolute-top-btn {
    position: absolute;
    height : 5vmin;
    width : 5vmin;
    bottom: 0;
    right: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    filter: invert(100%);
    opacity: 0.5;

    background-image : url('../img/arrow.png');  
    transform: rotate(90deg);
    color: white;
    background-size : cover;
    background-position : right;
    align-items: right;
    justify-content: right;
  }


  .Common-position-relative {
    position: relative;
  } 

  .Common-menu-btn {
    position: fixed;
    height : 6vmin;
    width : 6vmin;
    top: 3vmin;
    right: 3vmin;
    filter: invert(100%);
    background-image : url('../img/menu.png');  
    color: white;
    background-size : cover;
    /* background-position : right;
    align-items: center;
    justify-content: center; */
  }

  .Common-close-btn {
    height : 6vmin;
    width : 6vmin;
    filter: invert(100%);
    background-image : url('../img/close.png');  
    background-size : cover;
    text-align: right;
    align-items: right;
    justify-content: right;
    display: flex;
    flex-direction: row;

    margin-top: 2vmin;;
    margin-left: 2vmin;
    margin-bottom: 2vmin;
  }
  .Common-content-more-btn {

    /* display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; */

    color: var(--color-text-title);
    font-family: var(--font-common);
    font-size: var(--font-size-sm);
    background-color: var(--color-text);

    border-radius: 10%;
    /* width : 6vmin; */
    opacity: 0.5;
    display: inline-block;

  }

  .Common-content-more-btn:hover {
    background: var(--color-button-hover);
  }
  .Common-content-more-content {
    display: none;
    margin-top: 20px;
    margin-bottom: 20px;

    font-family: var(--font-common);
    color: var(--color-text);
    font-size: var(--font-size-sm);
    white-space: pre-wrap;
    padding-left: 20px;
    padding-right: 20px;



  }

  .Common-show-menu{
    display: flex;
    flex-direction: column;
    width: 90vh;
    max-width: 300px;
    height: 100vh;
    top: 0;
    right: 0;
    /* left: 10vh; */
    background-color: var(--color-background-menu);
    position: fixed;
    transition: 1s;
    /*opacity: 0.9;*/
    color:var(--color-text-menu);
  }
    
  .Common-hide-menu{
    display: none;
    width: 90vh;
    height: 100vh;
    position: fixed;
    left: -376px;
    transition: 1s;
  }

  .Common-nav-content {
    font-family: 'RIDIBatang';
    font-size: calc(10px + 2vmin);
    text-align: left;
    padding-left: 2vmin;
    padding-top: 1vmin;
    padding-bottom: vmin;
  }

  .Common-empty-20 {
    height: 20px;
  }

  .Common-empty-30 {
    height: 30px;
  }


